'use client'

import { useState, useEffect } from 'react'
import { ethers } from 'ethers'
import Link from 'next/link'
import { ArrowLeft, UserPlus, Users, Shield, X, Check } from 'lucide-react'

// Use @/ alias for imports
import SafeClubABI from '@/utils/SafeClub.json'
import contractAddress from '@/utils/contract-address.json'

export default function Members() {
    const [members, setMembers] = useState<string[]>([])
    const [newMemberAddress, setNewMemberAddress] = useState('')
    const [isOwner, setIsOwner] = useState(false)
    const [loading, setLoading] = useState(false)
    const [account, setAccount] = useState('')

    useEffect(() => {
        init()
    }, [])

    const init = async () => {
        try {
            const { ethereum } = window as any
            if (!ethereum) return

            const accounts = await ethereum.request({ method: 'eth_accounts' })
            if (accounts.length === 0) return

            setAccount(accounts[0])
            await checkOwner(accounts[0])
            await loadMembers()
        } catch (error) {
            console.error(error)
        }
    }

    const checkOwner = async (address: string) => {
        try {
            const provider = new ethers.providers.Web3Provider((window as any).ethereum)
            const contract = new ethers.Contract(
                contractAddress.address,
                SafeClubABI.abi,
                provider
            )
            const owner = await contract.owner()
            setIsOwner(owner.toLowerCase() === address.toLowerCase())
        } catch (error) {
            console.error(error)
        }
    }

    const loadMembers = async () => {
        try {
            const provider = new ethers.providers.Web3Provider((window as any).ethereum)
            const contract = new ethers.Contract(
                contractAddress.address,
                SafeClubABI.abi,
                provider
            )
            const membersList = await contract.getMembers()
            setMembers(membersList)
        } catch (error) {
            console.error(error)
        }
    }

    const addMember = async () => {
        if (!newMemberAddress || !ethers.utils.isAddress(newMemberAddress)) {
            alert('Please enter a valid address')
            return
        }

        try {
            setLoading(true)
            const provider = new ethers.providers.Web3Provider((window as any).ethereum)
            const signer = provider.getSigner()
            const contract = new ethers.Contract(
                contractAddress.address,
                SafeClubABI.abi,
                signer
            )

            const tx = await contract.addMember(newMemberAddress)
            await tx.wait()

            setNewMemberAddress('')
            await loadMembers()
            alert('Member added successfully!')
        } catch (error: any) {
            console.error(error)
            alert(error.message || 'Failed to add member')
        } finally {
            setLoading(false)
        }
    }

    const removeMember = async (address: string) => {
        if (!confirm(`Remove member ${address}?`)) return

        try {
            setLoading(true)
            const provider = new ethers.providers.Web3Provider((window as any).ethereum)
            const signer = provider.getSigner()
            const contract = new ethers.Contract(
                contractAddress.address,
                SafeClubABI.abi,
                signer
            )

            const tx = await contract.removeMember(address)
            await tx.wait()

            await loadMembers()
            alert('Member removed successfully!')
        } catch (error: any) {
            console.error(error)
            alert(error.message || 'Failed to remove member')
        } finally {
            setLoading(false)
        }
    }

    return (
        <div className="min-h-screen bg-gradient-to-br from-indigo-900 via-purple-900 to-pink-800">
            {/* Navigation */}
            <nav className="bg-black bg-opacity-30 backdrop-blur-lg border-b border-white border-opacity-10">
                <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                    <div className="flex justify-between items-center h-16">
                        <Link href="/">
                            <div className="flex items-center space-x-2 cursor-pointer">
                                <Shield className="w-8 h-8 text-cyan-400" />
                                <span className="text-2xl font-bold text-white">SafeClub</span>
                            </div>
                        </Link>
                        <Link href="/">
                            <button className="flex items-center space-x-2 text-white hover:text-cyan-400 transition-colors">
                                <ArrowLeft className="w-5 h-5" />
                                <span>Back</span>
                            </button>
                        </Link>
                    </div>
                </div>
            </nav>

            <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
                {/* Header */}
                <div className="text-center mb-12">
                    <div className="inline-flex items-center justify-center w-20 h-20 bg-gradient-to-br from-purple-500 to-pink-500 rounded-2xl mb-4">
                        <Users className="w-10 h-10 text-white" />
                    </div>
                    <h1 className="text-5xl font-bold text-white mb-4">Members Management</h1>
                    <p className="text-xl text-gray-300">
                        {isOwner ? 'Manage club members and access control' : 'View club members'}
                    </p>
                </div>

                {/* Add Member Section - Only for Owner */}
                {isOwner && (
                    <div className="bg-white bg-opacity-10 backdrop-blur-lg rounded-2xl p-8 mb-8 border border-white border-opacity-20">
                        <div className="flex items-center space-x-3 mb-6">
                            <UserPlus className="w-6 h-6 text-cyan-400" />
                            <h2 className="text-2xl font-bold text-white">Add New Member</h2>
                        </div>
                        <div className="flex flex-col md:flex-row gap-4">
                            <input
                                type="text"
                                placeholder="Enter member address (0x...)"
                                value={newMemberAddress}
                                onChange={(e) => setNewMemberAddress(e.target.value)}
                                className="flex-1 bg-white bg-opacity-10 border border-white border-opacity-20 rounded-xl px-6 py-4 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-cyan-500"
                            />
                            <button
                                onClick={addMember}
                                disabled={loading}
                                className="bg-gradient-to-r from-cyan-500 to-blue-500 hover:from-cyan-600 hover:to-blue-600 text-white px-8 py-4 rounded-xl font-semibold transition-all transform hover:scale-105 disabled:opacity-50 disabled:cursor-not-allowed"
                            >
                                {loading ? 'Adding...' : 'Add Member'}
                            </button>
                        </div>
                    </div>
                )}

                {/* Members List */}
                <div className="bg-white bg-opacity-10 backdrop-blur-lg rounded-2xl p-8 border border-white border-opacity-20">
                    <div className="flex items-center justify-between mb-6">
                        <h2 className="text-2xl font-bold text-white">Active Members</h2>
                        <div className="bg-gradient-to-r from-cyan-500 to-blue-500 px-4 py-2 rounded-lg">
                            <span className="text-white font-semibold">{members.length} Members</span>
                        </div>
                    </div>

                    {members.length === 0 ? (
                        <div className="text-center py-12">
                            <Users className="w-16 h-16 text-gray-500 mx-auto mb-4" />
                            <p className="text-gray-400 text-lg">No members added yet</p>
                        </div>
                    ) : (
                        <div className="space-y-3">
                            {members.map((member, index) => (
                                <div
                                    key={index}
                                    className="bg-white bg-opacity-5 rounded-xl p-5 flex items-center justify-between hover:bg-opacity-10 transition-all"
                                >
                                    <div className="flex items-center space-x-4">
                                        <div className="w-12 h-12 bg-gradient-to-br from-purple-500 to-pink-500 rounded-full flex items-center justify-center">
                                            <span className="text-white font-bold text-lg">{index + 1}</span>
                                        </div>
                                        <div>
                                            <p className="text-white font-mono text-lg">{member}</p>
                                            {member.toLowerCase() === account.toLowerCase() && (
                                                <span className="text-cyan-400 text-sm flex items-center space-x-1">
                          <Check className="w-4 h-4" />
                          <span>You</span>
                        </span>
                                            )}
                                        </div>
                                    </div>

                                    {isOwner && member.toLowerCase() !== account.toLowerCase() && (
                                        <button
                                            onClick={() => removeMember(member)}
                                            disabled={loading}
                                            className="bg-red-500 bg-opacity-20 hover:bg-opacity-30 text-red-400 p-2 rounded-lg transition-all disabled:opacity-50"
                                        >
                                            <X className="w-5 h-5" />
                                        </button>
                                    )}
                                </div>
                            ))}
                        </div>
                    )}
                </div>

                {/* Info Card */}
                <div className="mt-8 bg-cyan-500 bg-opacity-10 backdrop-blur-lg rounded-2xl p-6 border border-cyan-500 border-opacity-30">
                    <div className="flex items-start space-x-3">
                        <Shield className="w-6 h-6 text-cyan-400 mt-1" />
                        <div>
                            <h3 className="text-white font-semibold mb-2">About Members</h3>
                            <p className="text-gray-300 text-sm">
                                Only the contract owner can add or remove members. Members can deposit funds, create proposals, and vote on proposals.
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}